<template>
    <div class="dictionary-modal">
        <a-modal :title="title" :visible="regVisible" :mask=false :maskClosable=false @cancel="handleCancel"  :width="600" class="modalCls">
            <a-form-model ref="dictionaryForm" :model="dictionaryForm" :rules="rulesForm">
                <a-form-model-item label="字典分类标签" prop="tag">
                    <a-input v-model="dictionaryForm.tag" placeholder="请输入标签分类标签"></a-input>
                </a-form-model-item>
                <a-form-model-item label="字典key" prop="dictKey">
                    <a-input v-model="dictionaryForm.dictKey" placeholder="请输入字典key"></a-input>
                </a-form-model-item>
                <a-form-model-item label="字典值" prop="dictValue">
                    <a-input v-model="dictionaryForm.dictValue" placeholder="请输入字典值"></a-input>
                </a-form-model-item>
                <a-form-model-item label="是否启用">
                    <a-switch v-model="dictionaryForm.isEnabled"></a-switch>
                </a-form-model-item>
                <a-form-model-item label="是否删除">
                    <a-switch v-model="dictionaryForm.isDeleted"></a-switch>
                </a-form-model-item>
            </a-form-model>
            <template slot="footer">
                <a-button type="link" ghost @click="handleCancel">取消</a-button>
                <a-button type="primary" @click="handleOk">提交</a-button>
            </template>

        </a-modal>

    </div>
</template>

<script>
  export default {
    name: "dictionary-modal",
    props:{
      title:{
        type: String,
        default: "添加/编辑字典",
      },
      regVisible:{
        type:Boolean,
        default: false,
        required: true,
      },
      // 0为添加，1为编辑
      regType:{
        type:Number,
        default:0,
        required: true,
      },
      formContent: {
        type:Object,
        default:function () {
          return{
              dictKey:'',
              dictValue:'',
              tag:'',
          }
        }
      }
    },
    data(){
      return{
        dictionaryForm:{
            dictKey:'',
            dictValue:'',
            isEnabled:true,
            isDeleted:true,
        },
        rulesForm:{
            tag:[{ required: true, message: "请输入字典分类标签",trigger:'blur'}],
            dictKey:[{required: true,message:'请输入字典类型',trigger: 'blur'}],
            dictValue:[{required: true,message:'请输入字典表达式',trigger: 'blur'}]
        },
      }

    },
    methods:{
      handleOk(){
        this.$refs.dictionaryForm.validate(valid =>{
          if (valid){
            let form = Object.assign({},this.dictionaryForm,{isEnabled:this.dictionaryForm.isEnabled?1:0},{isDeleted:this.dictionaryForm.isDeleted?1:0});
            this.$emit('handleOk',{formData:form,regType:this.regType})
          } else {
            this.$message.warning('输入有误，请检查');
            return false
          }
        })
      },
      handleCancel(){
        this.$emit('handleCancel','点击取消')
      }


    },
    watch:{
      formContent:{
        handler:function(newVal){
          Object.assign(this.dictionaryForm,newVal);
        },
        deep:true
      }
    }
  }
</script>
<style scoped>

</style>