<template>
    <div class="baseRegExp pageSpace h100">
        <my-breadcrumb></my-breadcrumb>
        <div class="pageContent">
            <a-layout-content>
                <div class="baseRegExp">
                    <div class="tableTopTools clearfix">
                        <p class="toolName fl">工具栏：</p>
                        <div class="buttons fl">
                            <a-button type="primary" @click="addBaseReg">
                                <a-icon type="plus"></a-icon>添加字典
                            </a-button>
                        </div>
                    </div>
                    <div class="tableContent">
                        <a-table :columns="columns" :data-source="tableData" rowKey="id" :pagination="false">
                            <span slot="action" slot-scope="text,record">
                                 <a class="t-optColor-normal" @click="editBaseReg(record)">编辑</a>
                                 <a class="t-optColor-warning" @click="deleteBaseReg(record)">删除</a>
                            </span>
                            <span slot="isEnabled" slot-scope="text,record">
                                  <a-switch @click="switchEnableChange(record)" :checked="record.isEnabled" size="small" v-model="record.isEnabled"></a-switch>
                            </span>
                            <span slot="isDeleted" slot-scope="text,record">
                                  <a-switch @click="switchDeleteChange(record)" :checked="record.isDeleted" size="small" v-model="record.isDeleted"></a-switch>
                            </span>
                            <span slot="gmtCreate" slot-scope="text,record">
                                <span>{{record.gmtCreate | dateFormatter(1)}}</span>
                            </span>
                            <span slot="gmtModified" slot-scope="text,record">
                                <span>{{record.gmtModified | dateFormatter(1)}}</span>
                            </span>
                        </a-table>
                        <div class="ar" style="margin-top: 20px">
                            <a-pagination :v-model="curPage" :total="total" :page-size="pageSize" @change="pageChange"></a-pagination>

                        </div>
                    </div>
                    <dictionaryModal :regVisible="regVisible" :regType="regType" :formContent="formContent" @handleOk="handleOk"
                                     @handleCancel="handleCancel">
                    </dictionaryModal>

                </div>

            </a-layout-content>

        </div>
    </div>
</template>
<script>
    import dictionaryModal from '@/components/myModal/dictionary-modal'
    import moment from 'moment'
    export default {
        name:'dictionary',
        components: { dictionaryModal },
        data(){
            return{
                curPage:1,
                total:0,
                pageSize:10,
                regVisible:false, // 模态框状态
                regType:0,  // 添加或编辑状态

                columns:[
                    {
                        dataIndex:'tag',
                        title:'字典分类标签'
                    },
                    {
                        dataIndex:'dictKey',
                        title:'字典key'
                    },
                    {
                        dataIndex:'dictValue',
                        title:'字典value',
                        ellipsis:true,
                    },
                    {
                        dataIndex:'isEnabled',
                        title:'是否启用',
                        scopedSlots: { customRender: "isEnabled" },
                    },
                    {
                        dataIndex:'isDeleted',
                        title:'是否删除',
                        scopedSlots: { customRender: "isDeleted" },
                    },
                    {
                        dataIndex:'creator',
                        title:'创建者',
                        ellipsis: true
                    },
                    {
                        dataIndex:'gmtCreate',
                        title:'创建时间',
                    },
                    {
                        dataIndex:'gmtModified',
                        title:'修改时间'
                    },
                    {
                        dataIndex: "action",
                        title: "操作",
                        scopedSlots: { customRender: "action" },
                    },
                ],
                tableData:[],
                formContent:{
                    dictKey:'',
                    dictValue:'',
                    creator:'',
                    isEnabled:true,
                    isDeleted:true
                },

            }
        },
        methods:{
            //是否启用开关变化
            switchEnableChange(row){
                let obj = Object.assign({},{id:row.id,isEnabled:row.isEnabled === true ? 1:0});
                this.$API.updateDictionary(obj).then(res =>{
                    if (res.code === 1000){
                        this.$message.success('修改成功');
                        this.getDictionaryList()
                    }else {
                        this.$message.warning('修改失败')
                    }
                })
            },
            //是否删除开关变化
            switchDeleteChange(row){
                let obj = Object.assign({},{id:row.id,isDeleted:row.isDeleted === true ? 1:0});
                this.$API.updateDictionary(obj).then(res =>{
                    if (res.code === 1000){
                        this.$message.success('修改成功');
                        this.getDictionaryList()
                    }else {
                        this.$message.warning('修改失败')
                    }
                })
            },
            // 查询
            getDictionaryList(){
                let params = {
                    from:this.curPage,
                    size:this.pageSize,
                };
                this.$API.getDictionary(params).then(res =>{
                    if (res.code === 1000){
                        res.data.forEach(list => {
                            list.isEnabled = list.isEnabled === 1?true:false;
                            list.isDeleted = list.isDeleted === 1?true:false;
                        });
                        this.tableData = res.data.map(item => ({
                            ...item,
                            gmtCreate: new moment(item.gmtCreate).format("YYYY-MM-DD HH:mm:ss"),
                            gmtModified: new moment(item.gmtModified).format("YYYY-MM-DD HH:mm:ss")
                        }));
                        this.total = res.total;
                    } else {
                        this.tableData = [];
                        this.$message.warning('暂无数据')
                    }
                })

            },
            // 添加
            addBaseReg(){
                this.resetContent();
                this.regVisible = true;
                this.regType = 0;
            },
            // 提交
            handleOk(val){
                if (val.regType){
                    let patrams ={
                        tag:val.formData.tag,
                        dictKey:val.formData.dictKey,
                        dictValue:val.formData.dictValue,
                        isDeleted:val.formData.isDeleted,
                        isEnabled:val.formData.isEnabled
                    };
                    this.$API.updateDictionary(Object.assign({},patrams,{id:this.formContent.id})).then(res =>{
                        if (res.code === 1000){
                            this.$message.success('字典更新成功');
                            this.regVisible = false;
                            this.getDictionaryList()
                        } else {
                            this.$message.warning(res.message)
                        }
                    })
                } else {
                    this.$API.addDictionary(val.formData).then(res =>{
                        if (res.code === 1000){
                            this.$message.success("字典添加成功");
                            this.regVisible = false;
                            this.getDictionaryList()
                        } else {
                            this.$message.warning('字典添加失败')
                        }
                    })
                }
            },
            // 取消
            handleCancel(){
                this.regVisible = false;
                this.resetContent()
            },

            // 编辑
            editBaseReg(column){
                this.formContent = column;
                this.regType = 1;
                this.regVisible = true;
            },
            // 删除
            deleteBaseReg(row){
                const that = this;
                if (row.isEnabled === true){
                    that.$confirm({
                        title:'确认删除对话框',
                        okText:'确认',
                        cancelText:'取消',
                        content:'该条数据正在使用，确定要删除吗',
                        onOk(){
                            that.$API.deleteDictionary(row.id).then(res =>{
                                if (res.code === 1000){
                                    that.$message.success('删除成功');
                                    that.getDictionaryList()
                                } else {
                                    that.$message.warning('删除失败')
                                }
                            })
                        }

                    })
                } else {
                    that.$confirm({
                        title:'确认删除对话框',
                        content:() =><div style="color: red">删除后将不可恢复</div>,
                    okText:'确认删除',
                        cancelText:'取消删除',
                        onOk(){
                        that.$API.deleteDictionary(row.id).then(res =>{
                            if (res.code === 1000){
                                that.$message.success('删除成功');
                                that.getDictionaryList()
                            } else {
                                that.$message.warning('删除失败')
                            }
                        })
                    }
                })
                }

            },
            pageChange(){
                this.getDictionaryList()
            },
            // 重置form表单
            resetContent() {
                this.formContent = {
                    tag:'',
                    dictKey:'',
                    dictValue:'',
                    isEnabled:true,
                    isDeleted:true
                };
            },


        },
        created() {
            this.getDictionaryList()
        }

    }
</script>
